.main-docente-buscar-materia {
    width: 100%;
    height: 92vh;
    background-color: #fff4f4;
    text-align: left;
    display: flex;
    flex-direction: column; /* Asegura que los elementos estén en columnas */
    align-items: center; /* Centra horizontalmente */
}

.bloque-1-main-docente-buscar-materia {
    width: 100%;
}

.texto-bloque-1-main-docente-buscar-materia {
    margin-left: 6%;
    font-family: Montserrat;
    font-size: 20px;
    font-weight: 700;
    line-height: 24.38px;
    letter-spacing: -0.015em;
    color: #482828;
    cursor: pointer;
}

.bloque-2-main-docente-buscar-materia {
    width: 90%;
    height: 237px;
    gap: 0px;
    border-radius: 20px;
    background: #E1900D1A;
    border: 1px solid #420404;
    display: flex;
    align-items: center;
}

.bloque-abierto-main-docente-buscar-materia {
    width: 70px;
    height: 15px;
    top: -1px;
    left: -13px;
    padding: 9px 12px 7px;
    gap: 0px;
    border-radius: 20px 20px 20px 0px;
    opacity: 0px;
    background: #F4EB8F;
    font-family: Montserrat;
    font-size: 13px;
    font-weight: 700;
    line-height: 15.85px;
    letter-spacing: -0.015em;
    text-align: left;
    color: #420404;
}

.bloque-2-bloque-1-main-docente-buscar-materia {
    position: relative;
    margin-left: 3.5%;
    width: 20%;
    height: 181px;
    gap: 0px;
    border-radius: 20px;
    opacity: 0px;
    background: #FFFCEA;
    border: 1px solid #8E303059;
    display: flex;
    flex-direction: column; /* Asegura que los elementos estén en columnas */
    align-content: center;
    text-align: center;
}

.parrafo-1-bloque-2-bloque-1-main-docente-buscar-materia {
    position: relative;
    left: 2%;
    font-family: Montserrat;
    font-size: 24px;
    font-weight: 700;
    line-height: 29.26px;
    letter-spacing: -0.015em;
    color: #000000;
}

.parrafo-2-bloque-2-bloque-1-main-docente-buscar-materia {
    position: absolute;
    bottom: 10%;  /* Alinea el texto en la parte inferior */
    left: 0;
    right: 0;
    font-family: Montserrat;
    font-size: 15px;
    font-weight: 700;
    line-height: 18.29px;
    letter-spacing: -0.015em;
    color: #420404;
    cursor: pointer;
}
