.redes-sociales-contenedor-1 {
    width: 400px; /* Ancho del fondo */ 
    position: relative; 
    top: 180px; /* Ajusta la posición vertical según sea necesario */
    left: 2%; /* Ajusta la posición horizontal según sea necesario */
}

.redes-sociales-contenedor-2 { 
    width: 400px; /* Ancho del fondo */
    position: relative; 
    top: -350px; /* Ajusta la posición vertical según sea necesario */
    left: 35%; /* Ajusta la posición horizontal según sea necesario */
}

.redes-sociales-contenedor-3 { 
    width: 400px; /* Ancho del fondo */
    position: relative; 
    top: -880px; /* Ajusta la posición vertical según sea necesario */
    left: 68%; /* Ajusta la posición horizontal según sea necesario */
}

.redes-sociales-fondo {
    width: 400px; /* Ancho del fondo */
    height: 60px; /* Altura suficiente para el texto */
    background-color: rgb(91, 58, 58); /* Color de fondo */
    opacity: 1; 
    display: flex; 
    justify-content: center; /* Centrar horizontalmente el texto */
    align-items: center; /* Centrar verticalmente el texto */
    border-radius: 8px; /* Esquinas redondeadas */
}

.redes-sociales-texto {
    color: #FFFFFF; /* Color del texto */
    font-family: 'Moul', sans-serif; /* Fuente utilizada */
    font-size: 20px; /* Tamaño de la fuente */
    font-weight: 400; /* Grosor de la fuente */
    line-height: normal; /* Altura de línea normal */
    text-align: center; /* Alinear texto al centro */
    padding: 0 10px; /* Espaciado interno */
}

.contenedor-red-social {
    position: relative;
    top: 20px;
    width: 400px; /* Ancho del contenedor de la red social */
    height: 471px; /* Altura del contenedor de la red social */
    padding: 0; /* Elimina el espaciado interno para que el iframe ocupe todo el espacio */
    background: #FF4E4E; /* Color de fondo del contenedor */
    overflow: hidden; /* Asegúrate de que el iframe no sobresalga del contenedor */
}

.red-social-iframe {
    width: 100%; /* Ancho completo del contenedor */
    height: 100%; /* Altura completa del contenedor */
    border-radius: 8px; /* Esquinas redondeadas */
    border: none; /* Eliminar el borde por defecto del iframe */
    position: relative;
}


/* RESPONSIVE: Ajuste para móviles pequeños */
@media (max-width: 1342px) {
    
    .redes-sociales-contenedor-1 {
        width: 400px; /* Ancho del fondo */ 
        position: relative; 
        top: 180px; /* Ajusta la posición vertical según sea necesario */
        left: 15px; /* Ajusta la posición horizontal según sea necesario */
    }
    
    .redes-sociales-contenedor-2 { 
        width: 400px; /* Ancho del fondo */
        position: relative; 
        top: 350px; /* Ajusta la posición vertical según sea necesario */
        left: 15px; /* Ajusta la posición horizontal según sea necesario */
    }
    
    .redes-sociales-contenedor-3 { 
        width: 400px; /* Ancho del fondo */
        position: relative; 
        top: 600px; /* Ajusta la posición vertical según sea necesario */
        left: 15px; /* Ajusta la posición horizontal según sea necesario */
    }

    .redes-sociales-texto {
        font-size: 16px;
    }
}


