/* Estilos responsive para pantallas aún más pequeñas (teléfonos) */
@media (max-width: 480px) {
    .redes-sociales-fondo {
        width: 100%; /* Cambia a 100% para ajustar en pantallas pequeñas */
        max-width: 275px; /* Ancho máximo para pantallas pequeñas */
    }
    
    .contenedor-red-social {
        position: relative;
        width: 100%; /* Cambiar a 100% para adaptarse al contenedor */
        height: auto; /* Cambiar a auto para permitir que el iframe ajuste su altura */
        background: #FF4E4E;
    }

    .redes-sociales-contenedor-1,
    .redes-sociales-contenedor-2,
    .redes-sociales-contenedor-3 { 
        width: 100%; /* Cambiar a 100% para pantallas pequeñas */
        position: relative; 
        left: 0; /* Centrar contenedor en pantallas pequeñas */
    }
    
    .redes-sociales-contenedor-1 { 
        top: 180px; /* Ajustar según sea necesario */
    }

    .redes-sociales-contenedor-2 { 
        top: 240px; /* Ajustar según sea necesario */
    }

    .redes-sociales-contenedor-3 { 
        top: 350px; /* Ajustar según sea necesario */
    }

    .redes-sociales-texto {
        font-size: 16px; /* Disminuir tamaño de fuente */
    }
}


