/* Estilos generales */
html {
    scroll-behavior: smooth;
}

.main-festival-raices {
    width: 100%;
    height: auto;
    background-color: white;   
}

/* Imagen principal del festival, se adapta al ancho de la pantalla */
.personas-tocando-musical-festiva-raices {
    width: 100%;
}

/*Este es el diseño de la seccion 1*/
/* Ajusta el margen superior e inferior para darle espacio a la sección principal */
.section-1-festival-raices {
    margin-top: 4%;
    margin-bottom: 2%;
}

/* Bloque 1 - Contenedor de botones, con un fondo naranja */
.bloque-1-section-1-festival-raices {
    background-color: #FFA500; /* Fondo naranja */
    border-radius: 25px;
    height: 452px;
    margin-left: 5%;
    width: 40%; /* Ajustamos el ancho para hacer más espacio */
}

/* Botones dentro del bloque 1, con fondo oscuro y texto blanco */
.bloque-1-section-1-boton-festival-raices {
    width: 96%;
    height: 50px;
    margin: 2px;
    background-color: rgb(91, 58, 58); /* Color de fondo oscuro */
    color: white;
    font-weight: 900;
    font-size: 15px;
}

/* El primer botón tiene un margen superior adicional */
.bloque-1-section-1-boton-1-festival-raices {
    margin-top: 12px;
}

/* Bloque 2 - Contenedor de textos descriptivos, alineado a la izquierda */
.bloque-2-bloque-1-section-1-festival-raices {
    position: relative;
    text-align: left;
    width: 90%;
    margin-left: 8%;
    font-weight: 600;
    bottom: 30px;
}

/* Título dentro del bloque 2, con un tamaño grande */
.titulo-bloque-2-bloque-1-section-1-festival-raices {
    font-size: 30px;
}

/* Sub-bloque 2 - Contiene un subtítulo y un párrafo */
.bloque-2-bloque-2-section-1-festival-raices {
    position: relative;
    width: 75%;
    align-items: center;
    bottom: 35px;    
    margin-left: 110px;
}

/* Subtítulo del sub-bloque 2 */
.titulo-bloque-2-bloque-2-section-1-festival-raices {
    color: rgb(97, 0, 0); /* Color rojo oscuro */
    font-size: 20px;
}

/* Párrafo en el sub-bloque 2 */
.parrafo-bloque-2-bloque-2-section-1-festival-raices {
    position: relative;
    color: red; 
    bottom: 20px;
}


/* Este es el diseño de la sección 2 */
.section-2-festival-raices {
    width: 100%; /* La sección ocupará el 100% del ancho disponible */
}

/* Bloque 1 de la sección 2 - Configuración de alineación y márgenes */
.bloque-1-section-2-festival-raices {
    width: 92%;
    text-align: left; /* El contenido dentro del bloque estará alineado a la izquierda */
    font-weight: 600; /* El texto tendrá un peso de fuente semi-negrita */
    margin-left: 5%; /* Se aplica un margen izquierdo del 5% para separar el bloque del borde izquierdo de la página */
}


/* Este es el diseño de la sección 3 */
#section-3-festival-raices {
    width: 100%; /* La sección ocupará el 100% del ancho disponible */
}

/* Bloque 1 de la sección 3 - Configuración de alineación y márgenes */
.bloque-1-section-3-festival-raices {
    text-align: left; /* El contenido dentro del bloque estará alineado a la izquierda */
    font-weight: 600; /* El texto tendrá un peso de fuente semi-negrita */
    margin-left: 5%; /* Se aplica un margen izquierdo del 5% para separar el bloque del borde izquierdo de la página */
    width: 80%;
}

.bloque-2-section-3-festival-raices {
    width: 90%;
    height: 423px;
    margin-bottom: 30px;
    background-color: rgb(255, 217, 148); /* Color de fondo amarillo claro */
    border-radius: 30px; /* Bordes redondeados */
    text-align: left; /* El contenido dentro del bloque estará alineado a la izquierda */
    font-weight: 600; /* El texto tendrá un peso de fuente semi-negrita */
    margin-top: 2%; /* Margen superior del 2% */
    margin-left: 5%; /* Se aplica un margen izquierdo del 5% para separar el bloque del borde izquierdo de la página */
}

.proyecto-raices-imagen-festival-raices {
    width: 96%; /* Ancho de la imagen */
    height: 400px; /* Altura de la imagen */
    margin-left: 2%; /* Se aplica un margen izquierdo del 2% */
    margin-top: 1%; /* Margen superior del 1% */
}

.bloque-3-section-3-festival-raices {
    margin-left: 8%; /* Margen izquierdo del 8% */
}

.bloque-3-bloque-1-section-3-festival-raices {
    border-radius: 30px; /* Bordes redondeados */
    border: 2px solid black; /* Borde sólido, negro y de 2px de ancho */
    width: 95%; /* Ancho del bloque */
    background-color: #fdf1e1; /* Color de fondo muy claro */
    margin-bottom: 5%; /* Margen inferior del 5% */
    color: black; /* Color del texto */
    text-decoration: none; /* Sin subrayado en el texto */
}

.bloque-3-bloque-2-section-3-festival-raices {
    position: relative; /* Posicionamiento relativo */
    left: 38vh; /* Desplazamiento a la izquierda de 38vh */
    border-radius: 30px; /* Bordes redondeados */
    border: 2px solid black; /* Borde sólido, negro y de 2px de ancho */
    width: 95%; /* Ancho del bloque */
    background-color: #fdf1e1; /* Color de fondo muy claro */
    margin-bottom: 5%; /* Margen inferior del 5% */
}

.texto-bloque-3-section-3-festival-raices {
    margin-left: 10px; /* Margen izquierdo del 10px */
    text-align: center; /* Alineación del texto al centro */
    font-weight: 600; /* Texto semi-negrita */
    align-content: center; /* Alineación del contenido al centro */
}

.imagenes-section-3-festival-raices {
    width: 20%; /* Ancho de las imágenes */
    margin: 10px; /* Margen de 10px alrededor de las imágenes */
}

/*Diseño de la seccion 4*/
#section-4-festival-raices {
    margin-bottom: 40px;
}

.section-4-bloque-1-festival-raices {
    width: 90%;
    text-align: left;
    font-weight: 600;
    margin-left: 5%;
}

.section-4-bloque-2-festival-raices {
    width: 100%;
    background-color: rgb(204, 204, 204); 
    margin-top: 40px;
}

.imagenes-niveles-festival-raices {
    border-radius: 40px;
    width: 90%;
    margin-top: 1%;
    margin-left: 2%;
    margin-bottom: 1%;
}

/*Diseño de la seccion 5*/

#section-5-festival-raices {
    margin-bottom: 50px;
}

.section-5-bloque-1-festival-raices {
    width: 90%;
    margin-left: 5%;
    text-align: left;
    font-weight: 600;
    margin-bottom: 50px;

}

.section-5-bloque-2-festival-raices {
    width: 95%;
    margin-left: 3%;
}

.instrumentacion-imagenes-festival-raices { 
    height: 400px;
    margin-left: 4%;
}


/*Diseño de la seccion 6*/

#section-6-festival-raices {
    margin-left: 5%;
    margin-bottom: 50px;
}

.section-6-bloque-1-festival-raices {
    width: 90%;
    text-align: left;
    font-weight: 600;
    margin-bottom: 50px;
}

.section-6-bloque-2-festival-raices {
    border-radius: 40px;
    background-color: rgb(255, 217, 148); /* Color de fondo amarillo claro */
    width: 90%;
    text-align: left;
    font-weight: 600;
    margin-bottom: 50px;
}

.video-youtube-jornada-de-muestras-festival-raices {
    margin: 2%;
    width: 96%;
}

/*Diseño de la seccion 7*/

#section-7-festival-raices {
    margin-left: 5%;
    margin-bottom: 50px;
}


.section-7-bloque-1-festival-raices {
    width: 90%;
    text-align: left;
    font-weight: 600;
    margin-bottom: 50px;
}

.personas-en-la-ninez-tocando-musica-imagen-festival-raices {
    width: 100%;
}

/*Diseño de la seccion 8*/

.section-8-festival-raices {
    width: 90%;
    border-radius: 40px;
    margin-left: 5%; 
    margin-bottom: 50px; /* Margen izquierdo del 10px */
    background-color: rgb(255, 79, 79);
}

.section-8-bloque-1-festival-raices {
    width: 40%; /* Ancho del bloque */
    padding: 20px; /* Margen izquierdo del 8% */
}

.bloque-1-bloque-1-section8--festival-raices {
    border-radius: 30px; /* Bordes redondeados */
    border: 2px solid black; /* Borde sólido, negro y de 2px de ancho */
    width: 100%; /* Ancho del bloque */
    background-color: #b54d4d;  
    margin-bottom: 1%; /* Margen inferior del 5% */
    color: white; /* Color del texto */
    text-decoration: none; /* Sin subrayado en el texto */
}

.texto-bloque-1-bloque-1-section-8-festival-raices {
    margin-left: 10px; /* Margen izquierdo del 10px */
    text-align: center; /* Alineación del texto al centro */
    font-weight: 600; /* Texto semi-negrita */
    align-content: center; /* Alineación del contenido al centro */
}

.imagenes-section-8-festival-raices {
    width: 20%; /* Ancho de las imágenes */
    margin: 10px; /* Margen de 10px alrededor de las imágenes */
}

.section-8-bloque-2-festival-raices {
    width: 40%;
    border-radius: 30px;
    margin: 30px;
    margin-left: 11%;
}

.semillas-nivel-imagen {
    border-radius: 30px;
    width: 100%;
}

/*Diseño de la seccion 9*/

#section-9-festival-raices {
    margin-left: 5%;
    margin-bottom: 50px;
}

.section-9-bloque-1-festival-raices {
    text-align: left;
    font-weight: 500;
    margin-bottom: 50px;
}

.section-9-bloque-2-festival-raices {
    border-radius: 50px;
    width: 95%;
    background-color: rgb(244, 224, 189); /* Color de fondo amarillo claro */
}

.horarios-imagenes-festival-raices {
    margin: 30px;
}

/*Diseño de la seccion 10*/

#section-10-festival-raices {
    margin-left: 5%;
    margin-bottom: 50px;
}

.section-10-bloque-1-festival-raices {
    font-weight: 600;
    text-align: left;
}

.section-10-bloque-2-festival-raices {
    margin-top: 3%;
    margin-left: 12%;
}

.imagen-formulario-festival-raices {
    border-radius: 30px;
}


/* Media Queries para pantallas pequeñas */
@media (max-width: 768px) {
    /* Ajustes generales para pantallas tablet */
    .bloque-1-section-1-festival-raices {
        width: 90%; /* Ancho completo en pantallas pequeñas */
        margin-left: 5%; /* Reducimos el margen */
        height: auto; /* Permite que la altura se ajuste automáticamente */
    }

    /* Botones más pequeños y adaptables */
    .bloque-1-section-1-boton-festival-raices {
        font-size: 14px;
        height: 45px; /* Reducimos la altura del botón */
    }

    /* Títulos y textos más pequeños para tablets */
    .titulo-bloque-2-bloque-1-section-1-festival-raices {
        font-size: 24px;
    }

    .titulo-bloque-2-bloque-2-section-1-festival-raices {
        font-size: 18px;
    }

    .bloque-2-bloque-1-section-1-festival-raices, 
    .bloque-2-bloque-2-section-1-festival-raices {
        margin-left: 5%; /* Ajustamos el margen para centrarlos */
        width: 90%;
    }
}

/* Media Queries para pantallas muy pequeñas (móviles) */
@media (max-width: 480px) {
    /* En móviles, usamos el 100% del ancho */
    .bloque-1-section-1-festival-raices {
        width: 100%;
        margin-left: 0; /* Sin margen lateral */
        height: auto;
    }

    /* Botones aún más pequeños */
    .bloque-1-section-1-boton-festival-raices {
        font-size: 12px;
        height: 40px; /* Reducimos la altura del botón en móviles */
    }

    /* Títulos y textos adaptados para móviles */
    .titulo-bloque-2-bloque-1-section-1-festival-raices {
        font-size: 20px;
    }

    .titulo-bloque-2-bloque-2-section-1-festival-raices {
        font-size: 16px;
    }

    .bloque-2-bloque-1-section-1-festival-raices, 
    .bloque-2-bloque-2-section-1-festival-raices {
        margin-left: 5%;
        width: 100%; /* Usamos todo el ancho */
    }

    /* Centramos los textos en móviles */
    .bloque-2-bloque-2-section-1-festival-raices {
        text-align: center;
    }

    /* Párrafos más pequeños en móviles */
    .parrafo-bloque-2-bloque-2-section-1-festival-raices {
        font-size: 14px;
    }
}
