.boton-asistencia {    
    width: 60px;
    height: 25px;
    display: flex;
    align-items: center;
    padding: 10px 20px;
    border: none;
    background-color: white;
    border-radius: 5px;
    cursor: pointer;
    left: 33%;
    transition: background-color 0.3s ease;
    position: absolute;
}

.boton-asistencia .indicador-circular {
    text-align: right;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    margin-right: 10px;
    background-color: white; /* Circle is white */
    position: absolute;
    left: 3px; /* Default position at the left */
    transition: left 0.3s ease; /* Smooth transition when moving the circle */
}

.boton-asistencia .indicador-circular.true {
    left: 40px; /* Moves the circle to the right when present */
}

.boton-asistencia .indicador-circular.false {
    left: 7px; /* Moves the circle to the right when present */
}

.boton-asistencia.true {
    background-color: #4CAF50;
    color: white;
}

.boton-asistencia.false {
    background-color: #f44336;
    color: white;
}

.boton-asistencia:hover {
    opacity: 0.8;
}

.boton-asistencia:focus {
    outline: none;
}

