.fomulario-actualizar-evidencia-clase {
    position: absolute; /* Cambiado a absoluto */
    top: 50%; /* Alinea el borde superior del formulario en el medio de la pantalla */
    left: 50%; /* Alinea el borde izquierdo del formulario en el medio de la pantalla */
    transform: translate(-50%, -50%); /* Mueve el formulario hacia atrás su propia altura y ancho para centrarlo */
    margin: 20px auto; /* Centra horizontalmente */
    display: flex;
    flex-direction: column;
    gap: 10px; /* Espacio entre los elementos del formulario */
    max-width: 400px; /* Ancho máximo del formulario */
    padding: 20px; /* Espaciado interno */
    background-color: #fff; /* Color de fondo del formulario */
    border-radius: 10px; /* Bordes redondeados */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Sombra */
}

.fomulario-actualizar-evidencia-clase textarea {
    width: 96%;
    height: 110px;
    padding: 8px;  /* Añadido para separar el texto de los bordes */
    font-family: Montserrat;
    font-size: 14px;
    line-height: 1.5;
    resize: vertical; /* Permite que el campo se redimensione solo en la dirección vertical */
    overflow-y: auto; /* Agrega barras de desplazamiento si el texto excede la altura */
    white-space: pre-wrap; /* Asegura que el texto se envuelva correctamente */
}

.submit-actualizar-evidencia-clase {
    height: 35px;
    background-color: blue;
    color: white;
    cursor: pointer;
}

.cancel-actualizar-evidencia-clase {
    height: 35px;
    background-color: gray;
    color: white;
    cursor: pointer;
}

.formulario-agregar-foto-evidencia {
    background-color: white; /* Fondo oscuro con transparencia */
    color: black;
    padding: 30px;
    border-radius: 15px;
    position: fixed;
    top: 20%;
    left: 50%;
    transform: translateX(-50%);
    z-index: 100;
    max-width: 400px;
    width: 100%;
    box-shadow: 0 4px 12px #f76c6c; /* Sombra suave */
}

.formulario-agregar-foto-evidencia form {
    display: flex;
    flex-direction: column;
    gap: 15px; /* Espaciado entre los elementos del formulario */
}

.formulario-agregar-foto-evidencia input[type="file"] {
    padding: 10px;
    color: black;
    border: 2px solid #444;
    border-radius: 5px;
    font-size: 14px;
}

.formulario-agregar-foto-evidencia input[type="file"]:hover {
    color: white;
    background-color: #f76c6c; /* Color de fondo atractivo */
}

.formulario-agregar-foto-evidencia button {
    color: white;
    border: none;
    padding: 12px;
    border-radius: 8px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s ease, transform 0.2s ease; /* Efectos suaves de transición */
}

.subir-formulario-agregar-foto-evidencia {
    background-color: green;
}

.cancelar-formulario-agregar-foto-evidencia {
    background-color: gray;
}

.formulario-formulario-agregar-foto-evidencia button:hover {
    transform: translateY(-2px); /* Efecto de elevación */
}