/* Estilo principal */
.main-inicio {
    background-color: #D3D3D3;
    height: 150vh; /* Asegura que ocupe toda la altura de la pantalla */
    max-width: 200vh; /* Asegura que ocupe toda la anchura de la pantalla */
    padding: 20px; /* Espacio alrededor */
    position: relative; /* Para mantener el posicionamiento exacto de los elementos */
}

/* Rectángulo rojo */
.red-rectangle-festival {
    width: 35%; /* Responsivo: Ocupa el 80% del ancho disponible */
    max-width: 680px; /* Mantiene el ancho máximo en pantallas grandes */
    height: 30px; /* Mantiene la altura */
    position: relative; /* Mantiene la posición exacta */
    top: 51px; /* Mantiene la posición vertical */
    left: -30px;/* Mantiene la posición horizontal */
    gap: 0px;
    opacity: 1; /* Visibilidad */
    background-color: #fc4f4f; /* Color rojizo */
    display: flex;
    align-items: center;
    justify-content: center;
}

/* Texto dentro del rectángulo rojo */
.red-rectangle-festival-texto {
    font-family: Moul;
    font-size: 24px; /* Mantiene el tamaño de la fuente en pantallas grandes */
    font-weight: 400;
    line-height: 43.36px;
    letter-spacing: -0.015em;
    text-align: center;
    color: white;
    white-space: nowrap; /* Evita que el texto se corte en varias líneas */
}

.red-rectangle-redes {
    width: 20%; /* Responsivo: Ocupa el 80% del ancho disponible */
    max-width: 680px; /* Mantiene el ancho máximo en pantallas grandes */
    height: 30px; /* Mantiene la altura */
    position: relative; /* Mantiene la posición exacta */
    top: 138px; /* Mantiene la posición vertical */
    left: -30px;/* Mantiene la posición horizontal */
    gap: 0px;
    opacity: 1; /* Visibilidad */
    background-color: #fc4f4f; /* Color rojizo */
    display: flex;
    align-items: center;
    justify-content: center;
}

/* Texto dentro del rectángulo rojo */
.red-rectangle-redes-texto {
    font-family: Moul;
    font-size: 24px; /* Mantiene el tamaño de la fuente en pantallas grandes */
    font-weight: 400;
    line-height: 43.36px;
    letter-spacing: -0.015em;
    text-align: center;
    color: white;
    white-space: nowrap; /* Evita que el texto se corte en varias líneas */
}

/* Estilos responsive */
@media (max-width: 2000px) {
    .red-rectangle-festival {
        width: 80%; /* Mantiene el ancho en pantallas más pequeñas */
        max-width: 350px; /* Ajuste máximo para pantallas más pequeñas */
        height: 25px; /* Ajusta la altura para pantallas más pequeñas */
        top: 51px; /* Mantiene la misma posición vertical */
        left: -20px; /* Mantiene la posición horizontal lo más cerca posible */
    }

    .red-rectangle-festival-texto {
        font-size: 20px; /* Ajusta el tamaño de la fuente para pantallas más pequeñas */
        line-height: 30px; /* Ajusta la altura de línea para pantallas más pequeñas */
        white-space: nowrap; /* Mantiene el texto en una línea */
    }


    .red-rectangle-redes {
        width: 45%; /* Responsivo: Ocupa el 80% del ancho disponible */
        max-width: 200px; /* Mantiene el ancho máximo en pantallas grandes */
        height: 30px; /* Mantiene la altura */
