/* Barra de carga */
.barra-de-carga {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 4px;
    background-color: orange;
    animation: cargar 2s linear infinite;
}

@keyframes cargar {
    0% { width: 0%; }
    100% { width: 100%; }
}

/* Estilos principales */
.main-administrador-docentes-buscar-docente {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    background-color: #fff4f4;
    min-height: 85vh;
}

/* Tarjeta de información del docente */
.bloque-1-administrador-docentes-buscar-docente {
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    max-width: 900px;
    width: 100%;
    font-family: Arial, sans-serif;
    color: #333;
}

/* Estilo para los encabezados */
.bloque-1-administrador-docentes-buscar-docente h5 {
    margin: 10px 0 5px;
    font-size: 14px;
    color: #888;
    font-weight: bold;
    text-transform: uppercase;
}

/* Estilo para los valores */
.bloque-1-administrador-docentes-buscar-docente p {
    margin: 0 0 15px;
    font-size: 16px;
    color: #333;
}

/* Estilo para contenedor de datos */
.datos-docente {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}

/* Cada elemento en la columna */
.dato {
    flex: 1 1 calc(50% - 20px);
    min-width: 200px;
}

/* Estilo para mensajes de error */
.error {
    color: red;
    font-size: 16px;
    font-weight: bold;
    text-align: center;
}

.advertencia-eliminacion-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
}

.advertencia-eliminacion-modal {
    background-color: white;
    padding: 20px;
    width: 90%;
    max-width: 400px;
    border-radius: 8px;
    text-align: center;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.2);
}

.advertencia-eliminacion-modal p {
    margin-bottom: 20px;
    font-size: 1.1em;
}

.btn-confirmar, .btn-cancelar {
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    margin: 0 10px;
    cursor: pointer;
}

.btn-confirmar {
    background-color: red;
    color: white;
}

.btn-cancelar {
    background-color: gray;
    color: white;
}

/* Estilo para el botón de eliminar docente */
.btn-eliminar-docente {
    margin-top: 20px;
    padding: 10px 20px;
    background-color: red;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

/* Estilo para el mensaje de error y éxito */
.error-administrador-docentes, .success-administrador-docentes {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    padding: 15px;
    color: white;
    text-align: center;
    z-index: 999; /* Asegura que el mensaje esté por encima del contenido */
    opacity: 1; /* Transparencia inicial completa */
    transition: opacity 0.5s ease-in-out; /* Transición suave para desaparecer */
}

.error-administrador-docentes {
    background-color: #f44336; /* Color rojo */
}

.success-administrador-docentes {
    background-color: #4CAF50; /* Color verde */
}