.bloque-contenido-contenidos {
    margin-bottom: 4%;
    width: 93%;
    height: 17.5px;
    overflow: hidden; /* Oculta el contenido que sobrepasa */
    transition: max-height 0.3s ease; /* Usa max-height para una expansión controlada */
    cursor: pointer;
    padding: 4px 15px 3px 10px;
    gap: 0px;
    border-radius: 15px;
    opacity: 0px;
    background: #E1900D1A;
    border: 1px solid #953B3B4D;
    text-align: left;
    display: flex;
    align-items: center;
}

/* Expansión del contenedor al hacer clic */
.bloque-contenido-contenidos.expanded {
    max-height: none; /* Permite que el contenedor crezca para mostrar todo el contenido */
    height: auto;
    overflow: visible; /* Hace que el contenido expandido sea visible */
}

/* Expansión del contenedor al hacer clic */
