/* Expansión del contenedor al hacer clic */
.bloque-contenido-contenidos.expanded3 {
    max-height: none; /* Permite que el contenedor crezca para mostrar todo el contenido */
    height: auto;
    overflow: visible; /* Hace que el contenido expandido sea visible */
}

.bloque-contenido-contenidos.expanded p {
    white-space: normal; /* Permite saltos de línea cuando está expandido */
    overflow: visible;
    text-overflow: clip; /* Elimina los puntos suspensivos en el estado expandido */
    max-width: 90%; /* Limita el ancho al 80% del contenedor */
    width: 86%;
    word-wrap: break-word; /* Ajusta las palabras largas para evitar desbordamiento */
}

