.bloque-contenido-contenidos p {
    width: 90%; /* Hace que el texto ocupe el ancho completo del contenedor */
    max-height: 100%; /* Limita la altura para que coincida con la altura del contenedor */
    margin: 0;
    overflow: hidden;
    white-space: nowrap; /* Evita que el texto haga saltos de línea */
    text-overflow: ellipsis; /* Muestra puntos suspensivos (...) cuando el texto es demasiado largo */
    font-family: Montserrat;
    font-size: 15px;
    font-weight: 700;
    letter-spacing: -0.015em;
    text-align: left;
    color: #482828;
}

/* Estado inicial (flecha hacia abajo) */
.bloque-contenido-contenidos div {
    width: 0;
    height: 0;
    margin-left: 10%;
    border-left: 7.5px solid transparent; /* El borde izquierdo sigue siendo transparente */
    border-right: 7.5px solid transparent; /* El borde derecho sigue siendo transparente */
    border-top: 7.5px solid black; /* Borde superior visible para la flecha */
    transition: transform 0.3s ease; /* Transición suave para la rotación */
}

/* Estado expandido (flecha hacia arriba) */
.bloque-contenido-contenidos.expanded div {
    transform: rotate(180deg); /* Rota 180 grados para apuntar hacia arriba */
}

/* Estado expandido (flecha hacia arriba) */
.bloque-contenido-contenidos.expanded2 div {
    transform: rotate(180deg); /* Rota 180 grados para apuntar hacia arriba */
}

/* Estado expandido (flecha hacia arriba) */
.bloque-contenido-contenidos.expanded3 div {
    transform: rotate(180deg); /* Rota 180 grados para apuntar hacia arriba */
}

