.formulario-agregar-foto-estudiante {
    background-color: white; /* Fondo oscuro con transparencia */
    color: black;
    padding: 30px;
    border-radius: 15px;
    position: fixed;
    top: 20%;
    left: 50%;
    transform: translateX(-50%);
    z-index: 100;
    max-width: 400px;
    width: 100%;
    box-shadow: 0 4px 12px #f76c6c; /* Sombra suave */
}

.formulario-agregar-foto-estudiante form {
    display: flex;
    flex-direction: column;
    gap: 15px; /* Espaciado entre los elementos del formulario */
}

.formulario-agregar-foto-estudiante h2 {
    text-align: center;
    font-size: 30px;    
}

.formulario-agregar-foto-estudiante input[type="file"] {
    padding: 10px;
    color: black;
    border: 2px solid #444;
    border-radius: 5px;
    font-size: 14px;
}

.formulario-agregar-foto-estudiante input[type="file"]:hover {
    color: white;
    background-color: #f76c6c; /* Color de fondo atractivo */
}

.formulario-agregar-foto-estudiante button {
    color: white;
    border: none;
    padding: 12px;
    border-radius: 8px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s ease, transform 0.2s ease; /* Efectos suaves de transición */
}

.subir-formulario-agregar-foto-estudiante {
    background-color: green;
}

.cancelar-formulario-agregar-foto-estudiante {
    background-color: gray;
}

.formulario-agregar-foto-estudiante button:hover {
    transform: translateY(-2px); /* Efecto de elevación */
}

/* Estilo para el mensaje de error y éxito */
.error-administrador-estudiantes, .success-administrador-estudiantes {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    padding: 15px;
    color: white;
    text-align: center;
    z-index: 999; /* Asegura que el mensaje esté por encima del contenido */
    opacity: 1; /* Transparencia inicial completa */
    transition: opacity 0.5s ease-in-out; /* Transición suave para desaparecer */
}

.error-administrador-estudiantes {
    background-color: #f44336; /* Color rojo */
}

.success-administrador-estudiantes {
    background-color: #4CAF50; /* Color verde */
}



