.mensaje {
    position: fixed;
    top: 20px;
    left: 50%;
    transform: translateX(-50%);
    padding: 10px 20px;
    border-radius: 5px;
    z-index: 1000;
    color: white;
}

.mensaje.error {
    background-color: #f44336; /* Rojo */
}

.mensaje.exito {
    background-color: #4CAF50; /* Verde */
}

.fomulario-crear-semestre {
    position: absolute; /* Cambiado a absoluto */
    top: 50%; /* Alinea el borde superior del formulario en el medio de la pantalla */
    left: 50%; /* Alinea el borde izquierdo del formulario en el medio de la pantalla */
    transform: translate(-50%, -50%); /* Mueve el formulario hacia atrás su propia altura y ancho para centrarlo */
    margin: 20px auto; /* Centra horizontalmente */
    display: flex;
    flex-direction: column;
    gap: 10px; /* Espacio entre los elementos del formulario */
    max-width: 400px; /* Ancho máximo del formulario */
    padding: 20px; /* Espaciado interno */
    background-color: #fff; /* Color de fondo del formulario */
    border-radius: 10px; /* Bordes redondeados */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Sombra */
}


.fomulario-crear-semestre label {
    font-family: Montserrat;
    font-size: 16px;
}

.fomulario-crear-semestre input {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.fomulario-crear-semestre button {
    background-color: #E1900D;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.fomulario-crear-semestre button:hover {
    background-color: #C57D00; /* Color al pasar el cursor */
}

.boton-agregar-semestre {
    margin-left: 10px;
    background-color: transparent; /* O el color que prefieras */
    border: none;
    font-size: 20px; /* Ajusta el tamaño según sea necesario */
    cursor: pointer;
}

.boton-agregar-materia {
    margin-left: 10%;
    color: green;
    background-color: transparent; /* O el color que prefieras */
    border: none;
    font-size: 200px; /* Ajusta el tamaño según sea necesario */
    cursor: pointer;
    width: 20%;
    height: 130px;
    opacity: 0px;
    margin-left: 4%;
    text-align: center;
}

