.barra-de-carga {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 4px;
    background-color: orange;
    animation: cargar 2s linear infinite;
}

@keyframes cargar {
    0% { width: 0%; }
    100% { width: 100%; }
}

.lista-semestres-main-administrador-materias {
