.main-administrador-estudiantes {
    text-align: center;
    background-color: #fff4f4;
    display: flex;
    align-items: center; /* Centra horizontalmente */
    flex-direction: column; /* Asegura que los elementos estén en columnas */
    height: 90vh;
}

.estudiantes-container-administrador-estudiantes {
    width: 99%;
    height: 630px;
    border: 1px solid black;
    border-radius: 20px;
    display: flex; /* Activa flexbox */
    justify-content: center; /* Centra verticalmente */
    align-items: center; /* Centra horizontalmente */
    border: 1px solid #742929
}

.estudiantes-container-2-administrador-estudiantes {
    width: 95%;
    height: 570px;
    top: 21px;
    left: 33px;
    gap: 0px;
    border-radius: 20px;
    opacity: 0px;
    background: #E1900D1A;
    flex-direction: column; /* Asegura que los elementos estén en columnas */
    border: 1px solid #953B3B4D;
    flex-wrap: wrap; /* Permite que los elementos se envuelvan */
    overflow-y: scroll; /* Permite el desbordamiento en el eje Y */
    text-align: left;
}

.titulo-estudiantes-container-2-administrador-estudiantes {
    margin-left: 5%;
    color: #482828;

}

.barra-de-busquedas-administrador-estudiantes-bloque {
    width: 100%;
    display: flex;
    align-items: center; /* Centra horizontalmente */
    align-content: center;
    justify-content: center; /* Centra verticalmente */
    margin-bottom: 3%;
}

#barra-de-busquedas-administrador-estudiantes {
    width: 90%;
    padding: 12px 20px; /* Ajusta el espacio interior */
    margin: 20px auto; /* Centra la barra y añade espacio superior e inferior */
    border: 2px solid #E1900D; /* Borde en un color naranja suave */
    border-radius: 30px; /* Bordes redondeados para una apariencia moderna */
    background-color: #FFF8E1; /* Fondo claro para contrastar con el borde */
    font-size: 18px; /* Tamaño de texto adecuado */
    color: #333; /* Color del texto */
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1); /* Sombra suave para efecto de profundidad */
    outline: none; /* Quita el borde azul del navegador al hacer clic */
    transition: all 0.3s ease; /* Transición suave para los cambios */
}

/* Efecto de enfoque (hover) */
#barra-de-busquedas-administrador-estudiantes:focus,
#barra-de-busquedas-administrador-estudiantes:hover {
    border-color: #FFB74D; /* Cambia el color del borde */
    box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.2); /* Aumenta la sombra */
    background-color: #FFF3E0; /* Fondo ligeramente más oscuro al enfocar */
}

/* Estilo del texto dentro de la barra de búsqueda */
#barra-de-busquedas-administrador-estudiantes::placeholder {
    color: #999; /* Color más tenue para el texto del placeholder */
    font-style: italic; /* Fuente cursiva para diferenciar */
}

.add-student-button {
    margin-bottom: 20px;
    padding: 10px 20px;
    background-color: #E1900D;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.create-student-modal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    padding: 20px;
    border: 2px solid #E1900D;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

.create-student-modal input {
    width: 100%;
    margin: 8px 0;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.create-student-modal button {
    margin-top: 10px;
    padding: 10px 15px;
    background-color: #E1900D;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

/* Estilo para el mensaje de error y éxito */
.error-administrador-estudiantes, .success-administrador-estudiantes {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    padding: 15px;
    color: white;
    text-align: center;
    z-index: 999; /* Asegura que el mensaje esté por encima del contenido */
    opacity: 1; /* Transparencia inicial completa */
    transition: opacity 0.5s ease-in-out; /* Transición suave para desaparecer */
}

.error-administrador-estudiantes {
    background-color: #f44336; /* Color rojo */
}

.success-administrador-estudiantes {
    background-color: #4CAF50; /* Color verde */
}


.estudiante-card-administrador-estudiantes {
    width: 90%;
    height: 50px;
    background: #E1900D26;
    border: 1px solid #00000040;
    border-radius: 20px;
    display: flex; /* Activa flexbox */
    justify-content: center; /* Centra verticalmente */
    align-items: center; /* Centra horizontalmente */
    font-weight: 500;
    text-align: left;
    margin-left: 5%;
    margin-bottom: 10px;
    cursor: pointer;
}

.parrafo-estudiante-card-administrador-estudiantes {
    width: 100%;
    font-size: 35px;
    text-align: center;
    font-weight: 400;
    color: black;
}

.boton-crear-administrador-estudiante {
    margin-right: 3%;
}

