.materia-card-administrador-materias-buscar-materia {
    width: 90%;
    height: 50px;
    background: #E1900D26;
    border: 1px solid #00000040;
    border-radius: 20px;
    display: flex; /* Activa flexbox */
    justify-content: center; /* Centra verticalmente */
    align-items: center; /* Centra horizontalmente */
    font-weight: 500;
    text-align: left;
    margin-left: 5%;
    margin-bottom: 10px;
    cursor: pointer;
}

.parrafo-materia-card-administrador-materias-buscar-materia {
    width: 100%;
    font-size: 35px;
    text-align: center;
    font-weight: 400;
    color: black;
}

.barra-de-busquedas-administrador-materias-buscar-materia-bloque {
    width: 100%;
    display: flex;
    align-items: center; /* Centra horizontalmente */
    align-content: center;
    justify-content: center; /* Centra verticalmente */
    margin-bottom: 3%;
}

.barra-de-busquedas-administrador-materias-buscar-materia {
    width: 96.7%;
    padding: 12px 20px; /* Ajusta el espacio interior */
    margin: 20px auto; /* Centra la barra y añade espacio superior e inferior */
    border: 2px solid #E1900D; /* Borde en un color naranja suave */
    border-radius: 30px; /* Bordes redondeados para una apariencia moderna */
    background-color: #FFF8E1; /* Fondo claro para contrastar con el borde */
    font-size: 18px; /* Tamaño de texto adecuado */
    color: #333; /* Color del texto */
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1); /* Sombra suave para efecto de profundidad */
    outline: none; /* Quita el borde azul del navegador al hacer clic */
    transition: all 0.3s ease; /* Transición suave para los cambios */
}

/* Efecto de enfoque (hover) */
.barra-de-busquedas-administrador-materias-buscar-materia:focus,
.barra-de-busquedas-administrador-materias-buscar-materia:hover {
    border-color: #FFB74D; /* Cambia el color del borde */
    box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.2); /* Aumenta la sombra */
    background-color: #FFF3E0; /* Fondo ligeramente más oscuro al enfocar */
}

/* Estilo del texto dentro de la barra de búsqueda */
.barra-de-busquedas-administrador-materias-buscar-materia::placeholder {
    color: #999; /* Color más tenue para el texto del placeholder */
    font-style: italic; /* Fuente cursiva para diferenciar */
}


.boton-agregar-estudiante-por-materia {
    color: white;
    background-color: #E1900D;
    margin-left: 1%;
    border: none;
    font-size: 20px; /* Ajusta el tamaño según sea necesario */
    cursor: pointer;
    width: 30%;
    height: 40px;
    opacity: 0px;
}

.fomulario-agregar-estudiante-por-materia {
    position: absolute; /* Cambiado a absoluto */
    top: 50%; /* Alinea el borde superior del formulario en el medio de la pantalla */
    left: 50%; /* Alinea el borde izquierdo del formulario en el medio de la pantalla */
    transform: translate(-50%, -50%); /* Mueve el formulario hacia atrás su propia altura y ancho para centrarlo */
    margin: 20px auto; /* Centra horizontalmente */
    display: flex;
    flex-direction: column;
    gap: 10px; /* Espacio entre los elementos del formulario */
    max-width: 400px; /* Ancho máximo del formulario */
    padding: 20px; /* Espaciado interno */
    background-color: #fff; /* Color de fondo del formulario */
    border-radius: 10px; /* Bordes redondeados */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Sombra */
}

.fomulario-agregar-estudiante-por-materia label {
    font-family: Montserrat;
    font-size: 16px;
}

.fomulario-agregar-estudiante-por-materia select {
    width: 400px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.fomulario-agregar-estudiante-por-materia button {
    background-color: #E1900D;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.boton-eliminar-estudiante-por-materia {
    background-color: red;
    color: white;
    border-radius: 40px;
    cursor: pointer;
}

.boton-actualizar-materia {
    margin-left: 4%;
    color: black;  /* Color del texto */
    background-color: transparent; /* Fondo transparente */
    border: none;
    font-size: 50px; /* Tamaño del ícono y texto */
    cursor: pointer;
    width: 10%;
    height: 4px;
    opacity: 1;  /* Asegura que el botón sea visible */
    text-align: center;
}

.boton-actualizar-materia svg {
    width: 24px;   /* Ajusta el tamaño del ícono */
    height: 24px;  /* Ajusta el tamaño del ícono */
    fill: orange;  /* Asegura que el ícono sea de color negro */
}



