.observacion-clase-evidencias-buscar-clase-bloque p {
    width: 94%; /* Ajusta el ancho del contenedor */
    position: relative;
    left: 3%;
    font-family: Montserrat;
    font-size: 13px;
    font-weight: 400;
    line-height: 15.85px;
    letter-spacing: -0.015em;
    text-align: justified;
    text-underline-position: from-font;
    text-decoration-skip-ink: none;
    color: #420404;
    word-wrap: break-word;  /* Esto permite que el texto se ajuste y pase a la siguiente línea */
