}


.observacion-clase-evidencias-buscar-clase-bloque:hover {
    background-color: rgba(0, 0, 0, 0);
    color: white;
}

.fomulario-actualizar-evidencia-clase {
    position: absolute; /* Cambiado a absoluto */
    top: 50%; /* Alinea el borde superior del formulario en el medio de la pantalla */
    left: 50%; /* Alinea el borde izquierdo del formulario en el medio de la pantalla */
    transform: translate(-50%, -50%); /* Mueve el formulario hacia atrás su propia altura y ancho para centrarlo */
    margin: 20px auto; /* Centra horizontalmente */
    display: flex;
    flex-direction: column;
    gap: 10px; /* Espacio entre los elementos del formulario */
    max-width: 400px; /* Ancho máximo del formulario */
    padding: 20px; /* Espaciado interno */
    background-color: #fff; /* Color de fondo del formulario */
    border-radius: 10px; /* Bordes redondeados */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Sombra */
}

.fomulario-actualizar-evidencia-clase textarea {
    width: 96%;
    height: 110px;
    padding: 8px;  /* Añadido para separar el texto de los bordes */
    font-family: Montserrat;
    font-size: 14px;
    line-height: 1.5;
    resize: vertical; /* Permite que el campo se redimensione solo en la dirección vertical */
    overflow-y: auto; /* Agrega barras de desplazamiento si el texto excede la altura */
    white-space: pre-wrap; /* Asegura que el texto se envuelva correctamente */
}

.mensaje {
    padding: 10px;
    border-radius: 5px;
    margin-top: 10px;
    font-size: 16px;
}

.error {
    background-color: red !important;
