}

.submit-actualizar-evidencia-clase {
    height: 35px;
    background-color: blue;
    color: white;
    cursor: pointer;
}

.cancel-actualizar-evidencia-clase {
    height: 35px;
    background-color: gray;
    color: white;
    cursor: pointer;
}

