/* Media Query para pantallas pequeñas (max-width: 768px) */
@media (max-width: 768px) {
    .main-jardin-infantil {
        height: auto; /* Ajusta la altura al contenido */
        padding: 20px; /* Añade un poco de padding */
    }

    .section-1-jardin-infantil {
        height: auto; /* Permite que la sección se ajuste al contenido */
        margin-bottom: 20px; /* Espacio entre secciones */
    }

    .personas-en-la-ninez-sonrriendo {
        margin-top: 20px; /* Reduce el margen superior */
    }

    .section-2-jardin-infantil {
        height: auto; /* Ajusta la altura para que se adapte al contenido */
        padding: 20px; /* Añade un poco de padding */
    }

    .nuestra-vision-mision-jardin-infantil-texto {
        font-size: 24px; /* Reduce el tamaño de fuente */
        padding-top: 10px; /* Espacio superior ajustado */
    }

    .section-2-mision-jardin-infantil,
    .section-2-vision-jardin-infantil,
    .section-2-actividad-meritoria-jardin-infantil {
        width: 90%; /* Ampliar para ocupar más espacio */
        height: auto; /* Ajusta la altura para que se adapte al contenido */
        margin: 10px auto; /* Centra el bloque y añade margen */
        opacity: 1; /* Asegura que los elementos sean visibles */
    }

    .section-2-texto {
        font-size: 14px; /* Ajuste del tamaño de fuente */
        text-align: center; /* Centra el texto */
    }

    .section-3-jardin-infantil {
        padding: 20px; /* Añade un poco de padding */
    }

    .valores-bloque-1-jardin-infantil,
    .valores-bloque-3-jardin-infantil {
        height: auto; /* Ajusta la altura al contenido */
        padding: 10px; /* Añade un poco de padding */
    }

    .valores-texto-jardin-infantil {
        font-size: 14px; /* Ajuste del tamaño de fuente */
        width: 100%; /* Ancho completo */
        text-align: center; /* Centra el texto */
    }

    .section-4-jardin-infantil-imagenes {
        width: 100%; /* Ancho completo para imágenes */
        height: auto; /* Ajusta la altura al contenido */
        padding: 20px; /* Añade padding */
    }

    .section-4-bloque-2-jardin-infantil {
        width: 90%; /* Ajusta el ancho */
        font-size: 20px; /* Ajuste del tamaño de fuente */
        text-align: center; /* Centra el texto */
        margin: 0 auto; /* Centra el bloque */
    }

    .section-5-jardin-infantil {
        height: auto; /* Ajusta la altura al contenido */
        padding: 20px; /* Añade padding */
    }

    .section-5-bloque-4-jardin-infantil,
    .section-5-bloque-5-jardin-infantil,
    .section-5-bloque-6-jardin-infantil {
        width: 90%; /* Ajusta el ancho de los bloques */
        margin: 10px auto; /* Centra y añade margen */
        padding: 20px; /* Añade padding */
    }

    .section-5-bloque-7-jardin-infantil {
        width: 90%; /* Ajusta el ancho */
        margin: 10px auto; /* Centra y añade margen */
        text-align: center; /* Centra el texto */
    }

    .nino-en-columpio-imagen-jardin-infantil {
        width: 100%; /* Ancho completo para la imagen */
        height: auto; /* Ajusta la altura al contenido */
    }
}
