/* Estilos generales del main */
.main-quienes-somos {
    background-color: #D3D3D3;
    height: auto; /* Se ajusta al contenido */
    width: 100%; /* Ocupa toda la anchura de la pantalla */
    padding-top: 50px; /* Espacio alrededor */
    position: relative; /* Para mantener el posicionamiento exacto de los elementos */
    overflow: hidden; /* Evita que el contenido desborde el contenedor */
}

/* Imagen del profesor */
.profe-ensenando-imagen {
    position: relative;
    width: 100%; /* Ocupa todo el ancho de la ventana */
    height: 65vh; /* Mantiene la altura original */
    display: block; /* Asegura que la imagen se comporte como bloque */
    margin: 0 auto; /* Centra la imagen horizontalmente */
}

/* Sección de la fundación */
.fundacion-section-biliografia {
    width: 100%;
    height: auto; /* Cambiado a auto para ser responsivo */
    position: relative;
    background: #AF1A1A;
    margin-top: 0; /* Evita espacio adicional */
}

/* Imagen del logo */
.imagen-logo-quienes-somos {
    position: relative;
    right: 0; /* Mantiene el logo alineado a la derecha */
    top: 20px; /* Ajusta la posición */
    max-width: 80%; /* Limita el tamaño del logo */
    margin: 0 auto; /* Centra el logo */
}

/* Texto justificado */
.quienes-somos-texto-bibliografia {
    color: white;
    width: 90%; /* Aumenta el ancho para mayor responsividad */
    margin: 0 auto; /* Centra el elemento horizontalmente */
    position: relative;
    padding-bottom: 20px; /* Espacio debajo del texto */
    text-align: justify; /* Justificado */
    margin-top: 20px; /* Espacio superior */
}

/* Sección de estructura organizacional */
.estructura-organizacional-quienes-somos {
    position: relative;
    background-color: #272525;
    color: white;
    margin-top: 0; /* Evita espacio adicional */
    height: auto; /* Cambiado a auto para ser responsivo */
    padding: 30px; /* Espacio interno */
}

/* Texto dentro de la estructura organizacional */
.estructura-organizacional-texto {
    padding-top: 30px;
    padding-bottom: 20px; /* Espacio debajo del texto */
}

/* Textos de diferentes áreas */
.fundadores-texto,
.consejo-directivo-texto,
.representanto-legal-texto,
.fiscalizacion-control-texto {
    position: relative;
    text-align: center; /* Centra el texto dentro del contenedor */
    width: 100%;
    margin-top: 20px; /* Espacio superior */
    padding-bottom: 20px; /* Espacio debajo del texto */
}

/* Título y nombre de fundadores */
.fundadores-titulo,
.fundadores-nombre {
    width: 100%; /* Usa el 100% para ocupar todo el ancho del contenedor */
    margin: 0; /* Elimina márgenes */
    padding-bottom: 20px; /* Espacio debajo del texto */
}

/* Media Queries para Responsividad */
@media (max-width: 768px) {
    /* Imagen del profesor en pantallas medianas */
    .profe-ensenando-imagen {
        height: 50vh; /* Reduce la altura de la imagen en pantallas medianas */
    }

    /* Texto de la bibliografía en pantallas medianas */
    .quienes-somos-texto-bibliografia {
        width: 95%; /* Aumenta el ancho en pantallas medianas */
        margin-top: 10px; /* Ajusta el margen superior */
    }
    
    /* Imagen del logo en pantallas medianas */
    .imagen-logo-quienes-somos {
        max-width: 90%; /* Aumenta el tamaño máximo del logo en pantallas medianas */
        top: 10px; /* Ajusta la posición */
    }

    /* Estructura organizacional en pantallas medianas */
    .estructura-organizacional-quienes-somos {
        padding: 20px; /* Reduce el padding en pantallas medianas */
    }

    /* Texto en áreas específicas en pantallas medianas */
    .fundadores-texto,
    .consejo-directivo-texto,
    .representanto-legal-texto,
    .fiscalizacion-control-texto {
        font-size: 14px; /* Ajusta el tamaño de la fuente para mejor lectura */
    }

    /* Título y nombre de fundadores en pantallas medianas */
    .fundadores-titulo,
    .fundadores-nombre {
        font-size: 16px; /* Ajusta el tamaño del título */
    }
}

@media (max-width: 480px) {
    /* Texto de la bibliografía en pantallas muy pequeñas */
    .quienes-somos-texto-bibliografia {
        font-size: 14px; /* Tamaño de fuente más pequeño para pantallas muy pequeñas */
    }

    /* Texto en áreas específicas en pantallas muy pequeñas */
    .fundadores-texto,
    .consejo-directivo-texto,
    .representanto-legal-texto,
    .fiscalizacion-control-texto {
        font-size: 12px; /* Reduce aún más el tamaño de la fuente */
    }

    /* Título y nombre de fundadores en pantallas muy pequeñas */
    .fundadores-titulo,
    .fundadores-nombre {
        font-size: 14px; /* Tamaño de título más pequeño */
    }
}
