.header {
    position: sticky;
    top: 0; /* Mantiene el header pegado en la parte superior */
    z-index: 9999;
    background: #FFFFFF;
    width: 98vw; /* Cambiado a 100vw para ocupar todo el ancho de la ventana */
    height: 60px;
    display: flex;
    align-items: center;
    gap: 0;
    padding: 0 20px; /* Añadido padding para espacios laterales */
    box-sizing: border-box; /* Asegura que el padding no afecte al ancho total */
}

.logo {
    width: 152px;
    height: 38px;
    margin: 11px 0 0 34px; /* Se mantiene el margen original */
}

.menu-text {
    font-family: Montserrat, sans-serif;
    font-size: 13px;
    font-weight: 500;
    line-height: 15.85px;
    letter-spacing: -0.015em;
    text-align: center;
    color: #420404;
    background: white;
    text-decoration: none;
    cursor: pointer;
    margin: 0 10px; /* Añadido margen lateral para separación */
}

.inicio {
    width: 35px; 
    height: 16px; 
    margin-top: 11px; 
    margin-left: 25px; 
}

.quienes-somos {
    width: 100px; 
    height: 16px; 
    margin-top: 11px; 
    margin-left: 17px; 
}

.jardin-infantil {
    width: 218px;
    height: 16px;
    margin-top: 11px;
    margin-left: 3px;
}

.festival-raices {
    width: 194px;
    height: 16px;
    margin-top: 11px;
}

.iniciar-sesion-button {
    width: 130px;
    height: 40px;
    margin-top: 10px;
    margin-left: auto; /* Alinea a la derecha */
    gap: 0px;
    background-color: white;
    color: #420404;
    border-color: #420404;
    cursor: pointer;
    animation: ease-out 300ms;
}

.iniciar-sesion-texto {
    font-family: Montserrat, sans-serif;
    font-size: 15px;
    font-weight: 500;
    line-height: 18.29px;
    letter-spacing: -0.015em;
    text-align: center;
}

.registrarse-button {
    width: 156px;
    height: 40px;
    margin-top: 10px;
    margin-left: 10px; /* Para separar del botón de Iniciar Sesión */
    background-color: #420404;
    color: white;
    border: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}

.registrarse-text {
    font-family: Montserrat, sans-serif;
    font-size: 14px;
    font-weight: 500;
    line-height: 17.07px;
    letter-spacing: -0.015em;
    text-align: center;
}

/* Estilos responsive */
@media (max-width: 768px) {
    .header {
        flex-wrap: wrap; /* Permite que los elementos se envuelvan en pantallas más pequeñas */
        height: auto; /* Ajusta la altura automáticamente */
    }

    .menu-text {
        font-size: 12px; /* Reduce el tamaño de la fuente en pantallas más pequeñas */
    }

    .iniciar-sesion-button, .registrarse-button {
        width: 100%; /* Los botones ocuparán el 100% del ancho */
        margin-left: 0; /* Elimina el margen izquierdo en pantallas pequeñas */
        margin-top: 5px; /* Añade un poco de margen superior */
    }

    .registrarse-button {
        margin-left: 0; /* Elimina el margen izquierdo para que se alineen bien */
    }
}
