.main-administrador-docentes {
    text-align: center;
    background-color: #fff4f4;
    display: flex;
    align-items: center;
    flex-direction: column;
    height: 92vh;
}

.docentes-container-administrador-docentes {
    width: 99%;
    height: 630px;
    border: 1px solid #742929;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.docentes-container-2-administrador-docentes {
    width: 95%;
    height: 570px;
    top: 21px;
    left: 33px;
    gap: 0px;
    border-radius: 20px;
    opacity: 0px;
    background: #E1900D1A;
    flex-direction: column;
    border: 1px solid #953B3B4D;
    flex-wrap: wrap;
    overflow-y: auto;
    text-align: left;
}

.titulo-docentes-container-2-administrador-docentes {
    margin-left: 5%;
    color: #482828;
}

.barra-de-busquedas-administrador-docentes-bloque {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 3%;
}

#barra-de-busquedas-administrador-docentes {
    width: 90%;
    padding: 12px 20px;
    margin: 20px auto;
    border: 2px solid #E1900D;
    border-radius: 30px;
    background-color: #FFF8E1;
    font-size: 18px;
    color: #333;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    outline: none;
    transition: all 0.3s ease;
}

/* Efecto de enfoque (hover) */
#barra-de-busquedas-administrador-docentes:focus,
#barra-de-busquedas-administrador-docentes:hover {
    border-color: #FFB74D;
    box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.2);
    background-color: #FFF3E0;
}

#barra-de-busquedas-administrador-docentes::placeholder {
    color: #999;
    font-style: italic;
}

.error-administrador-docentes {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    padding: 15px;
    color: white;
    text-align: center;
    z-index: 999;
    background-color: #f44336;
    opacity: 1;
    transition: opacity 0.5s ease-in-out;
}

.docente-card-administrador-docentes {
    width: 90%;
    height: 50px;
    background: #E1900D26;
    border: 1px solid #00000040;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    text-align: left;
    margin-left: 5%;
    margin-bottom: 10px;
    cursor: pointer;
}

.parrafo-docente-card-administrador-docentes {
    width: 100%;
    font-size: 35px;
    text-align: center;
    font-weight: 400;
    color: black;
}
