/* Estilos generales del footer */
.footer-quienes-somos {
    width: 100%; /* El footer ocupa toda la anchura de la pantalla */
    background: #AF1A1A; /* Fondo rojo */
    padding-top: 20px; /* Espacio superior */
    padding-bottom: 60px; /* Espacio inferior */
    display: flex; /* Flexbox para alinear elementos */
    justify-content: space-between; /* Espacia los elementos entre sí */
    align-items: flex-start; /* Alinea los elementos al inicio verticalmente */
    flex-wrap: wrap; /* Permite que los elementos se adapten y envuelvan en pantallas pequeñas */
    color: white; /* Texto en color blanco */
}

/* Sección de texto de información de contacto */
.direccion-telefono-demas-informacion-fundacion {
    flex: 1; /* Ocupa el espacio restante en pantallas grandes */
    text-align: justify; /* Justifica el texto */
    margin-left: 30px; /* Margen izquierdo */
    margin-right: 20px; /* Margen derecho para separar del área de logos */
}

/* Logos de redes sociales e imágenes en la sección del footer */
.logos-imagenes-quienes-somos-footer {
    display: flex; /* Flexbox para alinear los logos horizontalmente */
    flex-direction: row; /* Mantiene los logos en una fila en pantallas grandes */
    justify-content: flex-end; /* Los alinea al lado derecho */
    align-items: center; /* Alinea los logos verticalmente en el centro */
    width: auto;
    margin-right: 30px; /* Margen derecho para separarlos del borde de la pantalla */
}

/* Estilo de los logos individuales */
.logos-footer-quienes-somos {
    width: 60px; /* Ajusta el tamaño de los logos */
    margin-left: 10px; /* Espacio entre los logos */
}

/* MEDIA QUERY: Ajusta el diseño para pantallas medianas y pequeñas */
@media (max-width: 768px) {
    .footer-quienes-somos {
        flex-direction: column; /* Cambia la dirección a columna en pantallas más pequeñas */
        align-items: center; /* Alinea todo el contenido en el centro */
    }

    .direccion-telefono-demas-informacion-fundacion {
        margin-left: 10px; /* Reduce el margen izquierdo en pantallas pequeñas */
        margin-right: 10px; /* Ajusta también el margen derecho */
        text-align: center; /* Centra el texto */
        font-size: 14px; /* Disminuye el tamaño del texto para que se ajuste mejor */
    }

    .logos-imagenes-quienes-somos-footer {
        margin-top: 20px; /* Añade espacio superior a la sección de logos */
        justify-content: center; /* Centra los logos horizontalmente */
    }

    .logos-footer-quienes-somos {
        width: 35px; /* Reduce el tamaño de los logos en pantallas pequeñas */
        margin-left: 10px; /* Mantiene algo de espacio entre los logos */
    }
}

/* MEDIA QUERY: Ajustes adicionales para pantallas muy pequeñas, como teléfonos */
@media (max-width: 480px) {
    .direccion-telefono-demas-informacion-fundacion {
        font-size: 12px; /* Disminuye el tamaño del texto aún más en pantallas muy pequeñas */
    }

    .logos-footer-quienes-somos {
        width: 30px; /* Ajusta el tamaño de los logos para pantallas muy pequeñas */
        margin-left: 5px; /* Reduce el margen lateral entre los logos */
    }
}


/* MEDIA QUERY: Ajustes adicionales para pantallas muy pequeñas, como teléfonos */
@media (max-width: 325px) {
    .direccion-telefono-demas-informacion-fundacion {
        font-size: 9px; /* Disminuye el tamaño del texto aún más en pantallas muy pequeñas */
    }

    .logos-footer-quienes-somos {
        width: 30px; /* Ajusta el tamaño de los logos para pantallas muy pequeñas */
        margin-left: 5px; /* Reduce el margen lateral entre los logos */
    }
}
