.header-docente {
    background: #FFFFFF;
    width: 100%; /* Cambiado a 100vw para ocupar todo el ancho de la ventana */
    height: 60px;
    display: flex;
    align-items: center;
    gap: 0;
    padding: 0 20px; /* Espacios laterales */
    box-sizing: border-box; /* Asegura que el padding no afecte al ancho total */
}

.logo {
    width: 152px;
    height: 38px;
    margin-right: 11px;
}

.bloque-entradas-header-docente {
    width: 75%;
    display: flex; /* Activa flexbox */
    justify-content: center; /* Centra verticalmente */
    align-items: center; /* Centra horizontalmente */
}

.bloques-header-docente {
    width: 215px;
    height: 40px;
    border-radius: 5px;
    border: 0.1px solid gray;
    gap: 0px;
    opacity: 0.9; /* Cambiado de 0 a 0.9 para hacer visible */
    animation-timing-function: ease-out;
    animation-duration: 300ms;
    display: flex; /* Activa flexbox */
    flex-direction: column; /* Asegura que los elementos estén en columnas */
    justify-content: center; /* Centra verticalmente */
    align-items: center; /* Centra horizontalmente */
    text-decoration: none;
    color: #420404;
    font-family: Montserrat;
    font-size: 18px;
    font-weight: 500;
    line-height: 21.94px;
    letter-spacing: -0.015em;
    text-align: center;
}

.bloque-1-header-docente,
.bloque-2-header-docente,
.bloque-3-header-docente {
    margin-left: 50px;
}

.bloques-header-docente:hover {
    background-color: rgb(91, 58, 58); /* Color de fondo */
    color: white;
}

.boton-docente-informacion-header-docente {
    position: relative;
    background-color: white;
    border-radius: 5px;
    border: 0.1px solid gray;
    width: 215px;
    height: 40px;
    left: 1%;
    gap: 0px;
    opacity: 1; /* Asegurando que sea visible */
    animation-timing-function: ease-out;
    animation-duration: 300ms;
    font-family: Montserrat;
    font-size: 15px;
    font-weight: 500;
    line-height: 18.29px;
    letter-spacing: -0.015em;
    text-align: center; 
    cursor: pointer;  
    color: #420404; 
}

.dropdown-menu-header-docente {
    background-color: white;
    position: fixed; /* Esto hace que el elemento se fije respecto a la ventana */
    border: 1px solid gray;
    border-radius: 15px;
    width: 215px;
    z-index: 9999; /* Asegúrate de que el menú esté sobre otros elementos */
    height: auto;
}

.menu-item-header-docente {
    position: relative;
    background-color: rgb(255, 228, 232);   
    border: 1px solid gray;
    border-radius: 12px;
    width: 80%;
    font-weight: 600;
    cursor: pointer;
}

.active {
    background-color: rgb(91, 58, 58); /* Color de fondo */
    color: white;
}
