/* Barra de carga */
.barra-de-carga {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 4px;
    background-color: orange;
    animation: cargar 2s linear infinite;
}

@keyframes cargar {
    0% { width: 0%; }
    100% { width: 100%; }
}

/* Estilos principales */
.main-administrador-estudiantes-buscar-estudiante {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    background-color: #fff4f4;
    min-height: 85vh;
}

/* Tarjeta de información del estudiante */
.bloque-1-administrador-estudiantes-buscar-estudiante {
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    max-width: 800px;
    width: 100%;
    font-family: Arial, sans-serif;
    color: #333;
}

/* Estilo para los encabezados */
.bloque-1-administrador-estudiantes-buscar-estudiante h5 {
    margin: 10px 0 5px;
    font-size: 14px;
    color: #888;
    font-weight: bold;
    text-transform: uppercase;
}

/* Estilo para los valores */
.bloque-1-administrador-estudiantes-buscar-estudiante p {
    margin: 0 0 15px;
    font-size: 16px;
    color: #333;
}

/* Estilo para contenedor de dos columnas */
.datos-estudiante {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}

/* Cada elemento en la columna */
.dato {
    flex: 1 1 calc(50% - 20px);
    min-width: 200px;
}

/* Estilo para mensajes de error */
.error {
    color: red;
    font-size: 16px;
    font-weight: bold;
    text-align: center;
}

.advertencia-eliminacion-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
}

.advertencia-eliminacion-modal {
    background-color: white;
    padding: 20px;
    width: 90%;
    max-width: 400px;
    border-radius: 8px;
    text-align: center;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.2);
}

.advertencia-eliminacion-modal p {
    margin-bottom: 20px;
    font-size: 1.1em;
}

.btn-confirmar, .btn-cancelar {
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    margin: 0 10px;
    cursor: pointer;
}

.btn-confirmar {
    background-color: red;
    color: white;
}

.btn-cancelar {
    background-color: gray;
    color: white;
}

/* Contenedor de la foto */
.foto-estudiante-container {
    width: 100%;
    min-height: 400px; /* Asegura que tenga suficiente altura */
    display: flex;
    justify-content: center; /* Centra horizontalmente */
    align-items: center;     /* Centra verticalmente */
}

/* Estilo de la foto */
.foto-estudiante {
    width: 100%;         /* El ancho será del 100% de su contenedor */
    height: auto;        /* La altura se ajustará automáticamente para mantener la relación de aspecto */
    max-height: 350px;   /* Máxima altura de 350px, ajustable según tus necesidades */
    object-fit: contain; /* Asegura que la imagen se ajuste sin recortes ni distorsión */
}


.foto-estudiante-container {
    position: relative; /* Necesario para posicionar el ícono de la cámara dentro del contenedor */
}

.foto-estudiante-container img {
    width: 350px;
    height: 350px;
    object-fit: cover;
    transition: filter 0.3s ease; /* Suaviza el cambio de brillo */
}

.foto-estudiante-container img:hover {
    filter: brightness(0.8);  /* Aplica un sombreado al pasar el cursor */
    cursor: pointer;  /* Cambia el cursor para indicar que es interactivo */
}

/* Ícono de la cámara que aparece solo al hacer hover */
.foto-estudiante-container img:hover::after {
    content: '';
    position: absolute;
    top: 50%;          /* Posiciona el ícono en el centro verticalmente */
    left: 50%;         /* Posiciona el ícono en el centro horizontalmente */
    transform: translate(-50%, -50%); /* Ajusta para centrar perfectamente */
    width: 50px;       /* Ancho del ícono */
    height: 50px;      /* Alto del ícono */
    background-color: #fff;
    border-radius: 10px; /* Bordes redondeados */
    box-shadow: 0 0 0 5px #000; /* Contorno del ícono */
}

/* El cuerpo de la cámara */
.foto-estudiante-container img:hover::after {
    background-color: #333;
    border-radius: 8px; /* Crea el cuerpo cuadrado de la cámara */
    width: 40px;
    height: 40px;
    position: absolute;
    top: 5px;
    left: 5px;
}

/* El lente de la cámara */
.foto-estudiante-container img:hover::before {
    content: '';
    position: absolute;
    top: 10px;
    left: 10px;
    width: 20px;
    height: 20px;
    background-color: #fff;
    border-radius: 50%; /* El lente de la cámara */
}


.formulario-agregar-foto-estudiante {
    background-color: white; /* Fondo oscuro con transparencia */
    color: black;
    padding: 30px;
    border-radius: 15px;
    position: fixed;
    top: 20%;
    left: 50%;
    transform: translateX(-50%);
    z-index: 100;
    max-width: 400px;
    width: 100%;
    box-shadow: 0 4px 12px #f76c6c; /* Sombra suave */
}

.formulario-agregar-foto-estudiante form {
    display: flex;
    flex-direction: column;
    gap: 15px; /* Espaciado entre los elementos del formulario */
}

.formulario-agregar-foto-estudiante input[type="file"] {
    padding: 10px;
    color: black;
    border: 2px solid #444;
    border-radius: 5px;
    font-size: 14px;
}

.formulario-agregar-foto-estudiante input[type="file"]:hover {
    color: white;
    background-color: #f76c6c; /* Color de fondo atractivo */
}

.formulario-agregar-foto-estudiante button {
    color: white;
    border: none;
    padding: 12px;
    border-radius: 8px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s ease, transform 0.2s ease; /* Efectos suaves de transición */
}

.subir-formulario-agregar-foto-estudiante {
    background-color: green;
}

.cancelar-formulario-agregar-foto-estudiante {
    background-color: gray;
}

.formulario-agregar-foto-estudiante button:hover {
    transform: translateY(-2px); /* Efecto de elevación */
}

/* Estilo para el mensaje de error y éxito */
.error-administrador-estudiantes, .success-administrador-estudiantes {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    padding: 15px;
    color: white;
    text-align: center;
    z-index: 999; /* Asegura que el mensaje esté por encima del contenido */
    opacity: 1; /* Transparencia inicial completa */
    transition: opacity 0.5s ease-in-out; /* Transición suave para desaparecer */
}

.error-administrador-estudiantes {
    background-color: #f44336; /* Color rojo */
}

.success-administrador-estudiantes {
    background-color: #4CAF50; /* Color verde */
}

