/* Carrusel.css */
.carousel {
  width: 98.8vw; /* Ocupa todo el ancho de la ventana */
  height: 362px; /* Mantiene la altura original */
  position: relative;
  top: 80px; /* Mantiene la posición */
  right: 20px; /* Asegura que esté alineado correctamente */
  gap: 0;
  opacity: 1;
  overflow: hidden;
}

.carousel-images {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  height: 100%;
  top: auto;
}
  
.carousel-image {
  width: 100%; /* Imagen ocupa el 100% del ancho del contenedor */
  height: auto; /* Mantiene la proporción de la imagen */
  object-fit: cover; /* Asegura que la imagen se ajuste bien al espacio disponible */
  transition: opacity 1s ease-in-out; /* Transición suave */
  opacity: 0; /* Inicialmente oculta */
}

.carousel-image.show {
  opacity: 1; /* Mostrar con transición */
}

/* Estilo para flechas */
.carousel-arrow {
  width: 42px; /* Ancho de las flechas */
  height: 59px; /* Alto de las flechas */
  background-color: rgba(217, 217, 217, 0.8); /* Fondo semi-transparente */
  border: none;
  position: absolute;
  top: 50%; /* Centrar en la altura */
  transform: translateY(-50%); /* Asegura que la flecha esté centrada verticalmente */
  cursor: pointer;
  opacity: 0.7; /* Un poco más visible */
  transition: all 300ms ease-out;
}

.carousel-arrow.prev {
  left: 8px; /* Mantiene la posición de la flecha anterior */
  border-radius: 8px 0 0 0;
}

.carousel-arrow.next {
  right: 8px; /* Mantiene la posición de la flecha siguiente */
  border-radius: 0 8px 0 0;
}

/* Estilo para las flechas de dirección */
.flecita-izquierda,
.flecita-derecha {
  position: relative;
  width: 0;
  height: 0;
  border-top: 19px solid transparent; /* Parte superior de la flecha */
  border-bottom: 19px solid transparent; /* Parte inferior de la flecha */
  border-right: 20px solid transparent; /* Parte derecha de la flecha */
}

.flecita-izquierda::after {
  content: '';
  position: absolute;
  left: 10px; /* Ajusta la posición horizontal */
  top: 50%; /* Centra verticalmente */
  transform: translateY(-50%) rotate(-45deg); /* Centra verticalmente y rota */
  width: 20px; /* Ancho de la flecha */
  height: 20px; /* Alto de la flecha */
  border-left: 2px solid #000000; /* Color y grosor del contorno */
  border-top: 2px solid #000000; /* Parte superior de la flecha */
}

.flecita-derecha {
  position: relative;
  width: 0;
  height: 0;
  border-top: 19px solid transparent; /* Parte superior de la flecha */
  border-bottom: 19px solid transparent; /* Parte inferior de la flecha */
  border-left: 20px solid transparent; /* Parte izquierda de la flecha */
}

.flecita-derecha::after {
  content: '';
  position: absolute;
  left: -20px; /* Ajusta la posición horizontal */
  top: 50%; /* Centra verticalmente */
  transform: translateY(-50%) rotate(225deg); /* Centra verticalmente y rota */
  width: 20px; /* Ancho de la flecha */
  height: 20px; /* Alto de la flecha */
  border-left: 2px solid #000000; /* Color y grosor del contorno */
  border-bottom: 2px solid #000000; /* Parte inferior de la flecha */
}

/* Estilos para el texto en las imágenes del carrusel */
.carousel-texto-1, .carousel-texto-2, .carousel-texto-3, .carousel-texto-4, .carousel-texto-5, .carousel-texto-6 {
  width: auto; /* Permite que el fondo se ajuste al contenido */
  height: auto; /* Permite que el fondo se ajuste al contenido */
  position: absolute;
  bottom: 11px; /* Mantiene el fondo en su lugar */
  left: 50%; /* Centra horizontalmente */
  transform: translateX(-50%); /* Centra horizontalmente */
  gap: 0px;
  opacity: 1; /* Asegúrate de que sea visible */
  font-family: Moul, sans-serif;
  font-size: 2vw; /* Tamaño de fuente responsivo */
  font-weight: 400;
  line-height: normal; /* Ajusta el line-height para que el texto no se extienda demasiado */
  letter-spacing: -0.015em;
  text-align: center;
  background: rgba(217, 217, 217, 0.8); /* Fondo semitransparente */
  color: #000000;
  border-radius: 15px;
  padding: 5px 15px; /* Reduce el padding para hacer el fondo más pequeño */
  font-weight: bolder;
}

/* Media Queries para hacer el carrusel responsive */
@media (max-width: 768px) {
  .carousel {
    height: 300px; /* Reduce la altura del carrusel en pantallas más pequeñas */
  }

  .carousel-arrow {
    width: 36px; /* Reduce el tamaño de las flechas */
    height: 50px;
  }

  .carousel-texto-1, .carousel-texto-2, .carousel-texto-3, .carousel-texto-4, .carousel-texto-5, .carousel-texto-6 {
    font-size: 4vw; /* Aumenta el tamaño de la fuente para pantallas más pequeñas */
  }
}

/* Pantallas de móviles (pantallas pequeñas) */
@media (max-width: 576px) {x
  .carousel {
    height: 250px; /* Ajusta la altura del carrusel para pantallas móviles */
  }

  .carousel-arrow {
    width: 30px; /* Ajusta el tamaño de las flechas para pantallas más pequeñas */
    height: 42px;
  }

  .carousel-texto-1, .carousel-texto-2, .carousel-texto-3, .carousel-texto-4, .carousel-texto-5, .carousel-texto-6 {
    font-size: 5vw; /* Tamaño de fuente más pequeño para dispositivos muy pequeños */
    position: absolute;
    bottom: 65px;
  }
}
