/* Estilos generales del main */
.main-verificar-docente {
    background-color: #D3D3D3;
    width: 100%; /* Ocupa toda la anchura de la pantalla */
    position: relative; /* Para mantener el posicionamiento exacto de los elementos */
    overflow: hidden; /* Evita que el contenido desborde el contenedor */
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 92vh;
}

.verificacion-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 90%;
    max-width: 500px;
    background-color: #ffffff;
    border: 2px solid #ccc;
    padding: 30px;
    border-radius: 10px;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  }
  
  .verificacion-exito, .verificacion-error {
    text-align: center;
  }
  
  .verificacion-exito h2, .verificacion-error h2 {
    font-family: 'Arial', sans-serif;
    font-size: 1.5rem;
    color: #333;
  }
  
  .boton-iniciar-sesion {
    margin-top: 20px;
    padding: 12px 24px;
    font-size: 1rem;
    background-color: orange;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .boton-iniciar-sesion:hover {
    background-color: darkorange;
  }
  
  .verificacion-container p {
    font-size: 1.2rem;
    color: #666;
  }