
.main-docente-registro-evidencias {
    width: 100%;
    height: 92vh;
    background-color: #faf1f1;
    text-align: left;
    display: flex;
    flex-direction: column; /* Asegura que los elementos estén en columnas */
    align-items: center; /* Centra horizontalmente */
}

.bloque-encabezado-registro-evidencias {
    display: flex;
    width: 90%;
}

.bloque-clases {
    width: 100%;
    overflow: scroll;
    height: 90vh;

}

.clase-item-docente-evidencias {
    width: 90%;
    height: 60px;
    top: 97px;
    left: 32px;
    gap: 0px;
    border-radius: 15px;
    opacity: 0px;
    background: #E1900D1A;
    border: 1px solid #953B3B4D;
    cursor: pointer;
    margin-bottom: 40px;
    margin-left: 5%;
}

.clase-item-docente-evidencias h3 {
    font-family: Montserrat;
    font-size: 15px;
    font-weight: 700;
    line-height: 12.29px;
    letter-spacing: -0.015em;
    text-align: left;
    text-underline-position: from-font;
    text-decoration-skip-ink: none;
    color: #000000;
    margin-left: 3%;
}

.clase-item-docente-evidencias p {
    font-family: Montserrat;
    font-size: 15px;
    font-weight: 400;
    line-height: 9.75px;
    letter-spacing: -0.015em;
    text-align: left;
    text-underline-position: from-font;
    text-decoration-skip-ink: none;
    color: #420404;
    margin-left: 3%;

}

.bloque-encabezado-registro-evidencias h2 {
    width: 25%;
    gap: 0px;
    opacity: 0px;
    font-family: Montserrat;
    font-size: 20px;
    font-weight: 700;
    line-height: 24.38px;
    letter-spacing: -0.015em;
    color: #482828;
}

.bloque-encabezado-registro-evidencias button {
    margin-top: 1%;
    background-color: green;
    color: white;
    border-radius: 20px;
    border: 3px solid black;
    width: 10%;
    height: 30px;
    cursor: pointer;
}

.formulario-crear-clase {
    position: absolute; /* Cambiado a absoluto */
    top: 50%; /* Alinea el borde superior del formulario en el medio de la pantalla */
    left: 50%; /* Alinea el borde izquierdo del formulario en el medio de la pantalla */
    transform: translate(-50%, -50%); /* Mueve el formulario hacia atrás su propia altura y ancho para centrarlo */
    margin: 20px auto; /* Centra horizontalmente */
    display: flex;
    flex-direction: column;
    gap: 10px; /* Espacio entre los elementos del formulario */
    max-width: 400px; /* Ancho máximo del formulario */
    padding: 20px; /* Espaciado interno */
    background-color: #fff; /* Color de fondo del formulario */
    border-radius: 10px; /* Bordes redondeados */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Sombra */
}

.submit-crear-clase {
    height: 35px;
    background-color: green;
    color: white;
    cursor: pointer;
}

.cancel-crear-clase {
    height: 35px;
    background-color: gray;
    color: white;
    cursor: pointer;
}

.bloque-1-main-administrador-materias {
    width: 99%;
    height: 446px;
    top: 116px;
    left: 36px;
    gap: 0px;
    border-radius: 20px;
    border: 1px solid #8E303059; /* Un solo borde */
    background: #E1900D1A;
    overflow: scroll;
    display: flex;
    flex-wrap: wrap; /* Permite que los elementos bajen a la siguiente línea */
    justify-content: flex-start; /* Alinea el contenido hacia el inicio */
    opacity: 1; /* Ajustado a 0 en lugar de 0px */
    margin-bottom: 60px;
}